path{
  fill:#55cc77;
  stroke:black;
  stroke-width:.5px;
}
.l{
  fill:#5577cc;
}
.g{
  fill:#bbff99;
}
.r{
  fill:#ffffff;
}
.f{
  fill:#bbbb55;
}
.o{
  fill:#888888;
}
.d1{
  height:5px;
  width:5px;
  border-radius:2.5px;
  position:absolute;
}
.d2{
  position:fixed;
  top:0;
  right:0;
  width:100%;
  background-color:#888888;
  z-index:4;
}
p{
  text-align:center;
  font-family:baskerville;
  margin-top:5px;
  margin-bottom:0;
}
.b{
  fill:#eeeebb;
}
.n{
  fill:#115522;
  font-size:20px;
  font-family:baskerville;
}
.d{
  fill:none;
}
.bt,.bt2{
  font-size:12px;
  display:inline-block;
  border-left:1px solid black;
}
.bt:last-child,.bt2:last-child{
  border-right:1px solid black;
}
.bt2{
  background-image:linear-gradient(rgba(192,192,192,.8),rgba(192,192,192,.8),rgba(192,192,192,0))
}
.e{
  stroke:black;
  stroke-width:0.5px;
  fill:#227755;
}
input{
  display:block;
  border-radius:5px;
  border:2px black solid;
  margin: 10px auto;
  width:200px;
  font-size:30px;
  font-family: Baskerville;
}
li{
  font-size:30px;
  font-family: Baskerville;
  text-align:center;
}
